import { useEffect, useRef, useState } from 'react';
import sel from 'classnames';
import s from './style.module.css'
import { Button, EmailInput, Input, PasswordInput } from '@ya.praktikum/react-developer-burger-ui-components';
import { NavLink, useNavigate } from 'react-router-dom'
import { UserObject, logoutUser } from '../../utils/api';
import { TUserState, fetchChangeProfile, fetchLoginUser, fetchLogout } from '../../services/reducers/user-info/user';
import { useSelector } from 'react-redux';
import { useDispatch } from '../../services/hooks';
import { RootState } from '../../services/store';


export const ProfilePage = () => {
    const inputRef = useRef(null);
    const dispatch = useDispatch();

    const activeLink = ({ isActive }: any) => isActive 
    ? sel("text text_type_main-medium", s.active__link) : sel("text text_type_main-medium text_color_inactive", s.link);

    const userDataStore = useSelector<RootState>(state => state.userStore.data) as UserObject;

    
    const [userData, setUserData] = useState({
        email: userDataStore.email,
        password: '',
        name: userDataStore.name
    });

    const handleChange = (e: React.SyntheticEvent) => {
        const {name, value} = e.target as HTMLInputElement;
        setUserData({
            ...userData,
            [name]: value
        });
    };

    const disabledButton = userData.email !== userDataStore.email || userData.name !== userDataStore.name || userData.password ? false : true;

    const updateUserInfo = () => {
            dispatch(fetchChangeProfile(userData))     
    }

    const resetUserInfo = () => {
        setUserData({
            email: userDataStore.email,
            password: '',
            name: userDataStore.name
        });
    }

    const handleLogout = () => {
        dispatch(fetchLogout())
    }

    return (
        <div className={s.page}>
            <div className={s.content__conteiner}> 
                <div className={s.profile__navigation}>
                    <nav id='profile-nav'>
                        <ul className={s.navigation__box}>
                            <li id='profilenav' className={sel(s.navigation__point)}>
                                <NavLink to='/profile'  className={activeLink}>
                                    
                                </NavLink>
                            </li>
                            <li className={sel(s.navigation__point)}>
                                <NavLink to='/profile/orders'  className={activeLink}>
                                     
                                </NavLink>
                            </li>
                            <li className={s.navigation__point}>
                                <NavLink to='/login' className={activeLink} onClick={handleLogout} >
                                    
                                </NavLink>
                            </li>
                        </ul>
                    </nav>
                    <p className={sel(s.subtitle, 'text text_type_main-default text_color_inactive')}>        </p>
                </div>
                <form className={s.container}>
            
                    <Input
                        type={'text'}
                        placeholder={''}
                        onChange={handleChange}
                        icon="EditIcon"
                        value={userData?.name}
                        name={'name'}
                        error={false}
                        ref={inputRef}
                        // onIconClick={onChangeName}
                        errorText={''}
                        size={'default'}
                        extraClass="ml-1"/>

                    <EmailInput
                        onChange={handleChange}
                        value={userData?.email}
                        name={'email'}
                        placeholder=""
                        isIcon={true}
                        extraClass="mb-2"
                    />

                    <PasswordInput
                        onChange={handleChange}
                        value={userData.password}
                        name={'password'}
                        extraClass="mb-2"
                        icon="EditIcon"
                    />
                    <div className={s.button__container}>
                        <Button htmlType="button" type="primary" size="medium" onClick={updateUserInfo} disabled={disabledButton}></ Button> 
                        <Button htmlType="button" type="primary" size="medium" onClick={resetUserInfo} disabled={disabledButton}>O</ Button>
                    </div>
                                     
                </form>   
            </div>     
       </div>
    )
}